	var util = require("../../utils/util.js"); //获取应用实例
    import service from '../../service.js';
    import mInput from '../../components/m-input.vue';
	import md5 from '../../utils/md5.js';

	var weburl = getApp().globalData.weburl;
	var shop_type = getApp().globalData.shop_type;
	var username = wx.getStorageSync('username');
	var md5_key = getApp().globalData.md5_key;
   
    export default {
        components: {
            mInput
        },
        data() {
            return {
                shop_type: shop_type,
				password:'',
				vcode:'',
				account:''
            }
        },
        methods: {
            findPassword() {
				/*
                if (this.email.length < 3 || !~this.email.indexOf('@')) {
                    uni.showToast({
                        icon: 'none',
                        title: '邮箱地址不合法',
                    });
                    return;
                }
				
                uni.showToast({
                    icon: 'none',
                    title: '已发送重置邮件至注册邮箱，请注意查收。',
                    duration: 3000
                });
				*/
			   var that = this
			   var shop_type = this.shop_type ;
			   var mobile = this.account ;
			   var password = this.password ;
			   var vcode = this.vcode ;
			   var md5password = md5(password,md5_key,true)
			   console.log('get code phone no:',mobile);
			   if (!util.checkPhoneNumber(mobile)) {
			       uni.showToast({
			           icon: 'none',
			           title: '手机号格式错误'
			       });
			       return;
			   }
			   if (password.length < 6 ) {
			       uni.showToast({
			           icon: 'none',
			           title: '密码最短为 6 个字符'
			       });
			       return;
			   }
			   if (!this.vcode) {
			       uni.showToast({
			           icon: 'none',
			           title: '验证码不能为空'
			       });
			       return;
			   }
			   wx.request({
			         url: weburl + '/api/web/user/find_password/find_password_act',
			         method: 'POST',
			         data: { 
			           username: mobile,
			           shop_type:shop_type,
					   password:password,
					   md5password:md5password,
					   code:vcode,
			         },
			         header: {
			           'Content-Type': 'application/x-www-form-urlencoded',
			           'Accept': 'application/json'
			         },
			         success: function (res) {
			           console.log(res.data);
						if(res.data.status=='y'){
							uni.showToast({
								icon: 'none',
								title:'密码修改完成',
								duration:1500,
							});
							uni.reLaunch({
							    url: '../hall/hall',
							});
						}else{
							uni.showToast({
								icon: 'none',
								title:'密码修改失败'+res.data.info,
								duration:2000,
							});
						}
			         }
			       })
            },
			getcode() {
				var that = this
				var shop_type = this.shop_type ;
				var phoneNo = this.account ;
				console.log('get code phone no:',phoneNo);
				if (!util.checkPhoneNumber(phoneNo)) {
				    uni.showToast({
				        icon: 'none',
				        title: '手机号格式错误'
				    });
				    return;
				}
			    wx.request({
			          url: weburl + '/api/web/user/find_password/sms_send',
			          method: 'POST',
			          data: { 
			            mobile: phoneNo,
			            shop_type:shop_type,
			          },
			          header: {
			            'Content-Type': 'application/x-www-form-urlencoded',
			            'Accept': 'application/json'
			          },
			          success: function (res) {
			            console.log(res.data);
						if(res.data.status=='y'){
							that.ret_info = res.data.data
							uni.showToast({
								 icon: 'none',
								 title:'验证码已发送',
								 duration:1500,
							});
						}else{
							uni.showToast({
								 icon: 'none',
								 title:'验证码获取失败'+res.data.info,
								  duration:2000,
							});
						}
						 
			          }
			        })
			},
        }
    }
